:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
html,
body {
  min-height: 100%;
}
body {
  background-color: var(--background);
  background-image: url("/flower.png");
  background-repeat: repeat;
  background-size: 240px 240px;
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
.panel-floral {
  background-image: url("/flower.png");
  background-repeat: repeat;
  background-size: 240px 240px;
}
.panel-soft {
  position: relative;
  overflow: hidden;
}
.panel-soft::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(255, 255, 255, 0.92);
  pointer-events: none;
}
.panel-soft > * {
  position: relative;
  z-index: 1;
}
@keyframes flash-fade-in {
  from {
    opacity: 0;
    transform: translateY(6px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.flash-in {
  animation: flash-fade-in 240ms ease-out;
}
